table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 80%;
  }

  th{
      background-color: var(--red);
      color:white;
  }
  
  td, th {
    border: 1px solid black;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: white;
    color:black;
  }
  tr:nth-child(odd) {
    background-color: whitesmoke;
    color:black;
  }