.course-box{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: whitesmoke;
    border-radius:20px;
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.342);
    width:100%;
    padding:1rem;
    margin:2rem;
    margin-bottom:6rem;
    opacity:0;
    transition: all .5s;
    &__more-info{
        background-color: cornflowerblue;
        color:white;
        padding:1rem 3.5rem;
        border-radius: 15px;
        text-decoration: none;
        transition: all .3s;
        backface-visibility: hidden;
        &:hover{
            background-color: var(--red);
            transform:translateY(-2px);
        }
        &:active{
            transform:translateY(0);
        }
    }
    &__image{
        width:15rem;
        height:10rem;
        object-fit: cover;
    }
    &__content{
        text-align: center;
        width:80%;
    }
}

.visible{
    opacity:1
}