.single-course-page{
    position:relative;
    overflow: hidden;
    padding-top:4rem;
    padding-left:2rem;
    color:white;
    &__title{
        font-size: 3rem;
        color: var(--red);
    }
    &__instructor{
        font-size:2rem;
        display: flex;
        align-items: center;
        i{
            margin-top:10px;
        }
    }
    &__image{
        position:absolute;
        top:20px;
        right:-20px;
        width:15rem;
        height:15rem;
        border-radius: 50%;
        border: 2px solid white;
        overflow: hidden;
        background-color: white;
        animation: rolling 2s forwards;
        
   
        img{
            width:100%;
            height:100%;
            object-position: center;
        }
    }
    &__back-btn{
        background-color:cornflowerblue;
        margin-top:1rem;
        display:inline-block;
        cursor: pointer;
        color:white;
        border-radius: 15px;
        padding:1rem 2rem;
    }
    .fas, .far{
        margin-right:.3rem;
    }
}

@keyframes rolling{
    0%{
        transform: rotate(180deg);
        right:-300px;
        opacity:0;
    }
    100%{
        transform:rotate(0);
        right:70px;
        opacity:1;
    }
}